<template>
  <!-- <div class="user">个人中心</div> -->
  <div class="user" v-if="user">
    <Panel>
      <template #header> <router-link to="/">主页/</router-link> </template>
      <template #content>
        <div class="content">
          <div>
            <el-avatar
              shape="square"
              size="medium"
              :src="user.avatar_url"
            ></el-avatar>
            <span>{{ user.loginname }}</span>
          </div>
          <div>{{ user.score }}积分</div>
          <div class="iconfont icon-github">
            <a :href="`https://github.com/${user.githubUsername}`"
              >@{{ user.githubUsername }}</a
            >
          </div>
          <div>注册时间{{ user.create_at | moment("from", "now") }}</div>
        </div>
      </template>
    </Panel>

    <Panel>
      <template #header> 最近创建的话题 </template>
      <template #content>
        <PostItem
          v-for="topic in user.recent_topics"
          :key="topic.id"
          v-bind="topic"
        />
      </template>
    </Panel>

    <Panel>
      <template #header> 最近参与的话题 </template>
      <template #content>
        <PostItem
          v-for="topic in user.recent_replies"
          :key="topic.id"
          v-bind="topic"
        />
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import PostItem from "../components/PostItem.vue";
export default {
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    }
  },
  components: { Panel, PostItem },

  // 在当前页切换当前页 写在created这样是不行的, 需要要监听      
  // created() {
  //   // 传递动态路由参数
  //   this.getUser(this.$route.params.username);
  //   console.log(this.$route);
  // },
  watch: {
    "$route.params.username": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true
    },
  },
  methods: {
    ...mapActions(["getUser"]),
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
};
</script>

<style lang='less'>
.content {
  > div {
    margin-bottom: 15px;
    color: #778087;
  }
}
</style>