import axios from '../../plugins/axios'
// 用户详情页
export default {
  state: () => ({
    // 对象的初始值一般写成 null 不写成 {}
    user: null
  }),
  mutations: {
    getUser(state, user) {
      state.user = user
    }
  },
  actions: {
    async getUser(context , loginname){
      const user = await axios.get(`/user/${loginname}`)
      console.log(user)
      context.commit('getUser' , user.data)
    }
  }
}