<template>
  <div class="app">
    <Header />

    <div class="main">
      <router-view></router-view>
    </div>

    <Footer />
  </div>
</template>
<script>
import { mapMutations } from 'vuex';
import Footer from "./components/Footer.vue";
import Header from "./components/Header.vue";
export default {
  components: { Footer, Header },
  created() {
    // 取, 在用户刚进入App里面的时候我就要看用户有没有登录
    // 登录了就更新 getInfo 中的 info
    const info = localStorage.getItem("info");
    if(info){
      this.getInfo(JSON.parse(info))
    }
  },
  methods: {
    ...mapMutations(['getInfo'])
  },
};
</script>

<style lang='less'>
.app {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.main {
  width: 90%;
  max-width: 1400px;
  margin: 20px auto;
  flex-grow: 1;
}
</style>