// 用户登录模块
import axios from '../../plugins/axios'
export default {
  state: () => ({
    info: null
  }),
  mutations: {
    getInfo(state, info) {
      state.info = info
    }
  },
  actions: {
    getInfo(context, token) {
      return new Promise((resolve, reject) => {
        // 当post需要接受参数时
        // 那么post 需要传参, 怎么传
        // 也就是发 post请求, 参数应该怎么传递
        // 就是把这个参数当成对象, 写在axios请求的第二个参
        axios.post('/accesstoken', { accesstoken: token }).then(res => {
          context.commit('getInfo', res)
          // 登录成功之后, 存(这个 info是自定义的, 随意)
          localStorage.setItem('info' , JSON.stringify(res))

          // 存 token
          localStorage.setItem('token' , token)
          resolve()
        }).catch(() => {
          console.log('出错了')
          reject()
        })
      })
    }
  }
}