import axios from '../../plugins/axios'
// vuex 文章列表模块
export default {
  state: () => ({
    list: []
  }),
  mutations: {
    getPosts(state, list) {
      state.list = list
    },
    loadMorePosts(state, list) {
      state.list = [...state.list, ...list]
    }
  },
  actions: {
    // 获取不同类别的文章列表数据
    // 1.初始请求
    async getPosts(context, tab) {
      // 当get需要接受参数时
      // 那么get 需要传参, 怎么传
      // 也就是发 get请求, 参数应该怎么传递
      // 用? &
      // 如: axios.get('/topics?pag=1&limit=20')
      // 我们这次获取的是不同类别的文章列表数据
      const list = await axios.get(`/topics?tab=${tab}`)
      // console.log(list.data)
      context.commit('getPosts', list.data)
    },
    // 请求文章列表有两种情况
    // 第一种就是上面的初始请求(1)
    // 2. 加载更多的请求
    // 依据传参 带不带page属性, 带page就是加载更多的请求, 不带page就是初始请求
    async loadMorePosts(context,{tab,page}){
      const list = await axios.get(`/topics?tab=${tab}&page=${page}&limit=20`)
      context.commit('loadMorePosts', list.data)
    }
  },
}