<template>
  <div class="post-item">
    <router-link :to="`/user/${author.loginname}`">
      <el-avatar
        class="img"
        shape="square"
        size="medium"
        :src="author.avatar_url"
      ></el-avatar
    ></router-link>
    <div v-if="reply_count" class="count-wrap">
      <span class="reply-count">{{ reply_count }}</span>
      <span>/</span>
      <span class="visit-count">{{ visit_count }}</span>
    </div>
    <span v-if="isHsaTag" :class="['tag', { active: top || good }]">{{
      tagText
    }}</span>
    <h3>
      <router-link :to="`/post/${id}`">{{ title }}</router-link>
    </h3>
    <span class="last-reply-count">{{
      last_reply_at | moment("from", "now")
    }}</span>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: [
    "author",
    "reply_count",
    "visit_count",
    "title",
    "last_reply_at",
    "top",
    "good",
    "tab",
    "id",
  ],
  computed: {
    isHsaTag() {
      // top 是置顶
      //  ||(或者)
      const page = this.$route.path;
      const isAllOrGoodPage =
        page === "/" || page === "/all" || page === "/good";
      return this.top ? true : isAllOrGoodPage ? true : false;
    },
    tagText() {
      return this.top
        ? "置顶"
        : this.good
        ? "精华"
        : Vue.filter("transformTab")(this.tab);
    },
  },
};
</script>

<style lang='less'>
.post-item {
  display: flex;
  align-items: center;
  padding: 10px 0px;
  border-bottom: 1px solid #ccc;
  &:hover {
    background-color: #f5f5f5;
  }
  h3 {
    margin: 0;
    // 不让标题换行
    white-space: nowrap;

    // 让标题多余的部分用 ... 显示, 隐藏起来
    text-overflow: ellipsis;
    overflow: hidden;

    font-size: 16px;
    color: #333;
    flex-grow: 1;
    margin-left: 10px;
    font-weight: normal;
    a {
      color: #333;
      // a标签的伪类
      &:visited {
        color: #999;
      }
      &:hover {
        text-decoration: underline;
      }
    }
  }
  // 不让这4个缩小
  .img,
  .count-wrap,
  .tag,
  .last-reply-count {
    flex-shrink: 0;
  }
  .tag {
    font-size: 12px;
    padding: 2px 4px;
    border-radius: 3px;
    background-color: #e5e5e5;
    color: #999;
    // 添加&代表的是他本身, 也就是tag。这样写这个标签既叫tag也叫active
    &.active {
      background-color: #80bd01;
      color: #fff;
    }
  }
  .count-wrap {
    width: 80px;
    font-size: 14px;
    margin: 0px 10px;
    text-align: center;
    .reply-count {
      font-size: 16px;
      color: #9e78c0;
    }
    .visit-count {
      color: #b4b4b4;
    }
  }
  .last-reply-count {
    font-size: 12px;
  }
}
</style>