<template>
  <div class="login">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span>登录</span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          hide-required-asterisk
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
        >
          <el-form-item label="用户名" prop="username">
            <el-input
              placeholder="用户名随便输入"
              style="width: 300px"
              type="text"
              v-model="ruleForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              placeholder="密码需要输入用户 token"
              style="width: 300px"
              type="password"
              v-model="ruleForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="login">登录</el-button>
          </el-form-item>
        </el-form></template
      >
    </Panel>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import Panel from "../containers/Panel.vue";
export default {
  data() {
    return {
      ruleForm: {
        username: "123",
        // 输入的密码需要是用户的 token, 因为用户名密码登录功能没有, 只有token登录功能
        password: "5aa9dfd4-f0aa-4217-8fca-4766c9300611",
      },
      rules: {
        username: [
          {
            required: true,
            trigger: "blur",
            message: "用户名不能为空",
          },
        ],
        password: [
          {
            required: true,
            trigger: "blur",
            message: "密码不能为空",
          },
        ],
      },
    };
  },
  components: {
    Panel,
  },
  methods: {
    ...mapActions(['getInfo']),   
    login() {
      console.log("登录");
      // validate 登录的校验函数,表单的校验方法
      // 参数为回调函数, 函数里面会接受两个参数值, 一个为布尔值, 一个为对象
      // 布尔值是为了验是否成功, 对象里面是拿到出错后的数据
      this.$refs.ruleForm.validate((boolean, object) => {
        if (boolean) {
          // 输入正确
          console.log("输入正确");
          this.getInfo(this.ruleForm.password).then(() => {
            console.log('登录成功了, 有效的token , 跳转到首页')
            this.$router.push('/')
          }).catch(() => {
            console.log('登陆失败, 无效的token')
            this.$message.error('错了哦，这是一个无效的token');
          })
        } else {
          // 输入不正确
          console.log("输入错误");
        }
      });
    },
  },
};
</script>

