<template>
  <!-- <div>{{ $route.params.tab || "all" }}文章列表页</div> -->
  <div v-if="postList.length">
    <PostItem v-bind="post" v-for="post in postList" :key="post.id" />

    <div v-if="loading" class="loading">正在加载中......</div>
  </div>
  <el-skeleton v-else :rows="18" animated />
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostItem from "../components/PostItem.vue";
import debounce from "lodash/debounce";
export default {
  components: { PostItem },
  data() {
    return {
      page: 2,
      loading: false,
    };
  },
  computed: {
    ...mapState({
      postList: (state) => state.postList.list,
    }),
    // tab(){
    //   return this.$route.params.tab || 'all'
    // }
  },
  watch: {
    "$route.params.tab": {
      handler(value) {
        // console.log(value)
        const res = value || "all";
        this.getPosts(res);

        // 切换的时候再还原
        this.page = 2
      },
      // handler(){
      //   this.getPosts({tab: this.tab});
      // },
      immediate: true,
    },
  },
  methods: {
    ...mapActions(["getPosts"]),
    ...mapActions(["loadMorePosts"]),

    // scrollFun(){
    //   // console.log('滚动条事件触发了')
    //   // 获取到滚动条距离底部的距离
    //   // 滚动条距离顶部的距离 window.scrollY 或者 document.documentElement.scrollTop
    //   // window 窗口的高度(文档的可视区)   window.innerHeight 或者 document.documentElement.clientHeight
    //   // 文档的总高度  document.documentElement.scrollHeight
    //   const scrollHeight = document.documentElement.scrollHeight
    //   const windowHeight = window.innerHeight || document.documentElement.clientHeight
    //   const scrollTop = window.scrollY || document.documentElement.scrollTop
    //   if(scrollHeight - windowHeight - scrollTop <= 200 && this.postList.length){
    //     // 因为进入页面的时候, 当数据没来的时候, 没有滚动条, 默认可能直接触发触底, 但是并不想触发 所以就添加了 this.postList.length 这个额外条件
    //     // 如果 <= 200 在这个范围我就触发了很多次, 所以我们需要做一个防抖功能
    //     console.log('触底')
    //     this.page ++
    //     this.loadMorePosts({tab: this.$route.params.tab, page: this.page});
    //   }
    // }

    // 做防抖
    scrollFun: debounce(async function () {
      // console.log('滚动条事件触发了')
      // 获取到滚动条距离底部的距离
      // 滚动条距离顶部的距离 window.scrollY 或者 document.documentElement.scrollTop
      // window 窗口的高度(文档的可视区)   window.innerHeight 或者 document.documentElement.clientHeight
      // 文档的总高度  document.documentElement.scrollHeight
      const scrollHeight = document.documentElement.scrollHeight;
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight;
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      if (
        scrollHeight - windowHeight - scrollTop <= 200 &&
        this.postList.length
      ) {
        // 因为进入页面的时候, 当数据没来的时候, 没有滚动条, 默认可能直接触发触底, 但是并不想触发 所以就添加了 this.postList.length 这个额外条件
        // 如果 <= 200 在这个范围我就触发了很多次, 所以我们需要做一个防抖功能
        if (!this.loading) {
          this.loading = true;
          console.log("触底");
          this.page++;
          await this.loadMorePosts({
            tab: this.$route.params.tab,
            page: this.page,
          });
          this.loading = false
        }
      }
    }, 300),
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFun);
  },
  // 销毁
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFun);
  },
  // created() {
  //   this.$store.dispatch("getPosts", this.$route.params.tab || "all");
  // },
};
</script>

<style>
.loading {
  height: 100px;
  text-align: center;
  line-height: 100px;
  font-size: 14px;
}
</style>