<template>
  <div class="header">
    <div class="header-inner">
      <h1>
        <router-link to="/"
          ><img
            src="https://game.gtimg.cn/images/yxzj/web201706/images/comm/logo.png"
            alt=""
        /></router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <router-link to="">新手入门</router-link>
        <router-link to="">API</router-link>
        <router-link to="">关于</router-link>
        <router-link v-if="!info" to="/login">登录</router-link>
        <div v-else>
          <el-avatar
            shape="square"
            size="medium"
            :src="info.avatar_url"
          ></el-avatar>
          <span @click="logout">退出</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState({
      info: (state) => state.info.info,
    }),
  },
  methods: {
    ...mapMutations(["getInfo"]),
    logout() {
      // 如果从 / 再往 / 跳转的话会报错的
      // 所以我们做一个判断 不在首页的情况下我跳转到首页, 在首页的情况下我就不用跳转到首页了
      if (this.$route.path !== "/") {
        this.$router.push("/"); 
      }
      // 清空本地存储
      localStorage.removeItem("info");
      // 清空 token
      localStorage.removeItem("token");
      // 清空 vuex 数据
      this.getInfo(null);
    },
  },
};
</script>

<style lang='less'>
.header {
  background: linear-gradient(to left, #494635, #444444);
  .header-inner {
    height: 60px;
    max-width: 1400px;
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h1 {
      width: 120px;
      margin: 0;
      img {
        width: 100%;
      }
    }
    .header-right {
      display: flex;
      align-items: center;
      a {
        color: #c9c9dd;
        margin-left: 20px;
        font-size: 14px;
        font-weight: 600;
        &:hover {
          color: #f8bd6b;
        }
      }
      > div {
        display: flex;
        align-items: center;
        color: #c9c9dd;
        font-size: 14px;
        font-weight: 600;
        margin-left: 20px;
        span {
          margin-right: 10px;
          &:hover {
            color: #f8bd6b;
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>