import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'

import PostList from '../views/PostList.vue'

import Post from '../views/Post.vue'

import User from '../views/User.vue'

import Login from '../views/Login.vue'

Vue.use(VueRouter)


// 路由懒加载
// 什么叫路由懒加载
// 我访问了才出现
const routes = [
  // 这个 /login 和 :tab 有冲突, 因为就可以写成 :tab/login ,所以我们把 /login 写在上面
  {
    path: '/login',
    component: Login
  },
  {
    // 一个网站有很多的详情页,每一个详情页都有一个单独的id
    // 这个 id 是个变量(包括很多的 id)
    path: '/post/:id',
    component: Post
  },
  {
    // 一个路由对象设置了哪些对象才能被称为页面
    // path(地址)   component(组件)
    path: '/',
    component: Home,
    children: [
      // 一上来就让首页默认展示内容, 我们需要设置一个默认子路由, 地址为空/组件还是内容的那一页
      {
        path: '',
        component: PostList
      },
      {
        path: ':tab',    // 这个 tab 是个变量(包括 "全部", "精华", "分享", "问答", "招聘", "客户端测试")
        component: PostList
      }
    ]
  },
  {
    path: '/user/:username',
    component: User
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
