<template>
  <!-- 分别要在 Panel.vue 里面的 panel-header 和 panel-content 写内容 -->
  <!-- 用到具名插槽 -->
  <!-- 父组件这边传插槽 -->
  <!-- 具名插槽的写法 v-slot='名字' , 简写 #名字 -->
  <Panel>
    <template #header>
      <router-link
        active-class="active"
        :class="['tab', {active: tab === '全部' && $route.path === '/'}]"
        :to="tab | transformTab"
        v-for="tab in tabs"
        :key="tab"
        >{{ tab }}</router-link
      >
    </template>

    <template #content> <router-view></router-view></template>
  </Panel>
</template>

<script>
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  data() {
    return {
      tabs: ["全部", "精华", "分享", "问答", "招聘", "客户端测试"],
    };
  },
};
</script>

<style lang='less'>
a.tab {
  color: #80bd01;
  margin-right: 25px;
  font-size: 14px;
  font-weight: 600;
}
a.active{
  background-color: #80bd01;
  border-radius: 3px;
  padding: 2px 4px;
  color: #fff;
}
</style>