<template>
  <div :class="['comment-item', { active: isActive }]" :id="id">
    <router-link :to="`/user/${author.loginname}`">
      <el-avatar
        shape="square"
        size="medium"
        :src="author.avatar_url"
      ></el-avatar>
    </router-link>
    <div class="comment-right">
      <div>
        <span>{{ author.loginname }}</span
        ><span class="position"
          ><a :href="`#${id}`"
            >{{ index + 1 }}楼<span class="iconfont icon-dian spot"></span
            ><span>{{ create_at | moment("from", "now") }}</span></a
          ></span
        ><span @click="upsClick" class="like"
          ><i v-if="is_uped" class="iconfont icon-dianzan_kuai"></i
          ><i v-else class="iconfont icon-dianzan"></i>{{ ups.length }}</span
        >
      </div>
      <div v-html="content"></div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["author", "index", "create_at", "content", "ups", "id", "is_uped"],
  data() {
    return {
      isActive: this.ups.length > 10,
    };
  },
  methods: {
    ...mapActions(["upsHandle"]),
    upsClick() {
      this.upsHandle(this.id);
    },
  },
};
</script>

<style lang='less'>
.comment-item {
  &.active {
    background-color: red;
  }
  display: flex;
  border-bottom: 1px solid #ccc;
  margin-top: 10px;
  .comment-right {
    margin-left: 20px;
    flex-grow: 1;
    img {
      width: 100%;
    }
    > div {
      display: flex;
      .position {
        flex-grow: 1;
        color: #08c;
      }
    }
    .like {
      cursor: pointer;
    }
  }
}
</style>