<template>
  <!-- <div>{{$route.params.id}}文章详情内容</div> -->
  <div v-if="post">
    <Panel>
      <template #content>
        <div class="post-header">
          <h2>
            <span class="tag" v-if="post.top || post.good">{{
              post.top ? "置顶" : "精华"
            }}</span
            >{{ post.title }}
          </h2>
          <div>
            <p>
              <span class="iconfont icon-dian spot"></span><span>发布于</span
              ><span>{{ post.create_at | moment("from", "now") }}</span
              ><span class="iconfont icon-dian spot"></span
              ><span>作者{{ post.author.loginname }}</span
              ><span class="iconfont icon-dian spot"></span
              ><span>{{ post.visit_count }}次浏览</span
              ><span class="iconfont icon-dian spot"></span
              ><span>来自{{ post.tab | transformTab }}</span>
            </p>
            <el-button
              @click="coiiect"
              size="mini"
              :type="post.is_collect ? 'info' : 'success'"
              v-if="info"
              >{{ post.is_collect ? "取消收藏" : "收藏" }}</el-button
            >
          </div>
        </div>
        <div class="post-content" v-html="post.content"></div>
      </template>
    </Panel>
    <Panel>
      <template #header>
        <span>{{ post.reply_count }} 回复</span>
      </template>
      <template v-if="post.replies.length" #content>
        <PostCommentItem
          v-for="(comment, index) in post.replies"
          :key="comment.id"
          v-bind="{ ...comment, index }"
        />
      </template>
    </Panel>
    <Panel v-if="info">
      <template #header>
        <span>添加回复</span>
      </template>
      <template #content>
        <!-- md 编辑器 -->
        <textarea
          v-model="content"
          name=""
          id=""
          cols="30"
          rows="10"
        ></textarea>
        <el-button type="primary" @click="add">回复</el-button>
      </template>
    </Panel>
  </div>
  <el-skeleton v-else :rows="8" animated />
</template>

<script>
import Panel from "../containers/Panel.vue";
import PostCommentItem from "../components/PostCommentItem.vue";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      content: "",
    };
  },
  components: {
    Panel,
    PostCommentItem,
  },
  computed: {
    ...mapState({
      // state.post 拿到的是我们在index.js里面导出的post模块, 导出的这个post模块拿到的是post.js里面的模块的对象{} , 在.post才是拿到里面的 post
      post: (state) => state.post.post,

      info: (state) => state.info.info,
    }),
  },
  methods: {
    ...mapActions(["getPost", "collectHandle", "addComment"]),
    coiiect() {
      this.collectHandle({
        id: this.post.id,
        is_collect: !this.post.is_collect,
      });
    },
    add() {
      if (this.content.trim()) {
        this.addComment({ id: this.post.id, content: this.content });
      }
    },
  },
  created() {
    this.getPost(this.$route.params.id);
  },
};
</script>

<style lang='less'>
.post-header {
  border-bottom: 1px solid #ccc;
  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .tag {
    font-size: 12px;
    color: #fff;
    padding: 2px 4px;
    background-color: #80bd01;
    border-radius: 3px;
    margin-right: 10px;
  }
  p {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #838383;
    .spot {
      margin: 0;
      font-size: 22px;
      color: #838383;
    }
  }
}
.post-content {
  img {
    width: 100%;
  }
}
</style>