import axios from '../../plugins/axios'
// 文章详情模块
export default {
  state: () => ({
    // 对象的初始值一般写成 null 不写成 {}
    post: null
  }),
  mutations: {
    getPost(state, post) {
      state.post = post
    },
    changCollect(state, bool) {
      state.post.is_collect = bool
    },
    // action 是点赞行为
    // 值是 up 的话就是点赞
    // 值是 down 的话就是取消点赞
    changeUps(state, { id, action }) {
      const userId = JSON.parse(localStorage.getItem('info')).id
      const currentReply = state.post.replies.find(el => el.id === id)
      // 除了要修改 is_uped 之外, 点击的个数也要变化, 就是要修改 ups, ups 里面存的是点赞用户的id
      // 当用户点赞时, 需要将当前用户的 id push 到 ups 数组内
      // 当用取消户点赞时, 需要将当前用户的 id 从 ups 数组内删除
      if (action === 'up') {
        currentReply.is_uped = true
        currentReply.ups.push(userId)
      } else {
        currentReply.is_uped = false
        currentReply.ups = currentReply.ups.filter(el => el !== userId)
      }
    }
  },
  actions: {
    async getPost(context, id) {
      // const post = await axios.get(`/topic/${id}`)
      // console.log(post)
      // context.commit('getPost' , post.data)

      // 请求文章详情分两种情况
      // 1. 当用户没有登录时,去请求文章详情的话, 不需要带 accesstoken 参数
      // 2. 当用户登录之后再去请求文章详情的话, 需要带 accesstoken 参数了
      const token = localStorage.getItem('token')
      const url = token ? `/topic/${id}?accesstoken=${token}` : `/topic/${id}`
      const post = await axios.get(url)
      console.log(post)
      context.commit('getPost', post.data)
    },
    // 收藏或者取消收藏
    // id 是文章 id ,   is_collect 是 true 就去做收藏请求 是 false 就做 取消收藏
    // post /topic_collect/collect 收藏主题
    // post /topic_collect/de_collect 取消主题
    async collectHandle(context, { id, is_collect }) {
      const token = localStorage.getItem('token')
      if (is_collect) {
        // 收藏主题
        await axios.post('/topic_collect/collect', { topic_id: id, accesstoken: token })
        context.commit('changCollect', true)
      } else {
        // 取消收藏
        await axios.post('/topic_collect/de_collect', { topic_id: id, accesstoken: token })
        context.commit('changCollect', false)
      }
    },
    // post /reply/:reply_id/ups 为评论点赞或取消点赞
    async upsHandle(context, id) {
      const token = localStorage.getItem('token')
      const res = await axios.post(`/reply/${id}/ups`, { accesstoken: token })
      context.commit('changeUps', { id: id, action: res.action })
    },
    // post /topic/:topic_id/replies 新建评论
    // id是你要评论的那个人的id  content是评论的内容
    async addComment({dispatch}, { id, content}) {
      const token = localStorage.getItem('token')
      await axios.post(`/topic/${id}/replies`, { accesstoken: token, content: content })
      // 请求的返回值 {success: true, reply_id: '5433d5e4e737cbe96dcef312'} 基本上只有一个新增之后的评论的id
      // 请求成功之后后台就更新了
      // 其实前端只需要展示出来 添加了就可以了, 但是有的时候信息较少无法添加
      // 我们可以重新发 getPost 请求, 也就是将整个文章详情重新请求, 那么新增的评论也就更新了
      dispatch('getPost' , id)
    }
  }
}